.back {
  padding: 10% 0;
  .heading {
    margin-bottom: 4%;
    color: #2196f3;
    display: flex;
    align-items: center;
    .h1 {
      font-weight: 12;
      text-decoration: underline;
    }
    .h2 {
      font-weight: 12;
      font-size: large;
      padding-left: 1%;
    }
  }

  .sentence {
    width: 100%;
    height: 400px;
    background-color: #f5f5f5;
    border-radius: 50px;
    margin-left: 5%;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    justify-content: center;
    grid-gap: 3rem;
    .item {
      height: 300px;
      width: 300px;
    }
  }
}
